.data-tables {
  width: 50%;
  margin-bottom: 30px;
  margin-top: 46px;
}
.data-tables__wrapper {
  display: flex;
  gap: 32px;
  margin-bottom: 30px;
}
.data-tables table {
  border-collapse: collapse;
  width: 100%;
}
.data-tables table a {
  font-family: Nunito Sans;
  font-size: 14px;
  font-weight: 600;
  line-height: 19.1px;
  text-align: center;
  text-decoration-line: underline;
  text-decoration-style: solid;
  color: #289A9C;
  transition: color 0.3s;
}
@media (hover: hover) {
  .data-tables table a:hover {
    color: #157778;
  }
}
.data-tables th, .data-tables td {
  padding: 14px;
  text-align: left;
}
.data-tables td {
  padding: 14px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}
.data-tables tr:nth-child(even) {
  background-color: #F8F8F8;
}
.data-tables th {
  background-color: #f2f2f2;
}
@media only screen and (max-width: 1400px) {
  .data-tables th, .data-tables td {
    padding: 10px 4px;
    text-align: left;
  }
  .data-tables td {
    padding: 10px 4px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
}
@media only screen and (max-width: 1100px) {
  .data-tables {
    width: 100%;
  }
}
@media only screen and (max-width: 800px) {
  .data-tables__wrapper {
    gap: 22px;
    flex-direction: column;
  }
}