*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	min-height: 100%;
	position: relative;
	scroll-behavior: smooth;
	scrollbar-gutter: stable;
}

body {
	color: #3F3F3F;
	font-family: "Nunito Sans", sans-serif;
	font-optical-sizing: auto;
	font-size: 14px;
	font-weight: 500;
	line-height: 26px;

	&.is-popup-open {
		overflow: hidden;
	}
}

.wrapper {
	display: flex;
	flex-flow: column nowrap;
	min-height: 100vh;
	overflow: hidden;
}

.container {
	width: 100%;
	max-width: 1920px;
	margin: 0 auto;
	padding: 0 240px;

	@media only screen and (max-width: 1750px) {
		padding: 0 40px;
	}

	@media only screen and (max-width: 1199px) {
		padding: 0 20px;
	}
}

img {
	max-width: 100%;
	vertical-align: bottom;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

*:focus {
	outline: none !important;
}

canvas {
	display: block;
}

html,
body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
	transition: color .3s;
}

p {
	margin: 1em 0;

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}
}

button {
	background-color: transparent;
	padding: 0;
	border: none;
	user-select: none;
	cursor: pointer;
}

img {
	max-width: 100%;
}

.svg-icon {
	fill: currentColor;
}

.icon {
	display: flex;
	flex-shrink: 0;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
}

a,
input,
label,
button {
	-webkit-backface-visibility: hidden;
	-moz-backface-visibility: hidden;
	backface-visibility: hidden;
	-webkit-tap-highlight-color: transparent
}

a,
input,
label,
span,
button::selection {
	background: transparent
}

a,
input,
label,
button::-moz-selection {
	background: transparent
}

/* Modern Browsers */
::placeholder {
	font-family: Nunito Sans;
	font-size: 16px;
	font-weight: 400;
	line-height: 21.82px;
	text-align: center;
	color: #3F3F3F;
	transition: color .3s;
}

/* WebKit, Edge */
::-webkit-input-placeholder {
	font-family: Nunito Sans;
	font-size: 16px;
	font-weight: 400;
	line-height: 21.82px;
	text-align: center;
	color: #3F3F3F;
	transition: color .3s;
}

/* Firefox 4-18 */
:-moz-placeholder {
	font-family: Nunito Sans;
	font-size: 16px;
	font-weight: 400;
	line-height: 21.82px;
	text-align: center;
	color: #3F3F3F;
	opacity: 1; /* Override default Firefox opacity */
	transition: color .3s;
}

/* Firefox 19+ */
::-moz-placeholder {
	font-family: Nunito Sans;
	font-size: 16px;
	font-weight: 400;
	line-height: 21.82px;
	text-align: center;
	color: #3F3F3F;
	opacity: 1; /* Override default Firefox opacity */
	transition: color .3s;
}

/* IE 10-11 */
:-ms-input-placeholder {
	font-family: Nunito Sans;
	font-size: 16px;
	font-weight: 400;
	line-height: 21.82px;
	text-align: center;
	color: #3F3F3F;
	transition: color .3s;
}

/* Edge */
::-ms-input-placeholder {
	font-family: Nunito Sans;
	font-size: 16px;
	font-weight: 400;
	line-height: 21.82px;
	text-align: center;
	color: #3F3F3F;
	transition: color .3s;
}