form {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 40px 160px;
  box-shadow: 0px 0px 50px 0px rgba(40, 154, 156, 0.2);
  max-width: 662px;
  gap: 16px;
  background-color: #fff;
}
form .form__wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 14px;
}
form h1 {
  font-size: 30px;
  font-weight: 700;
  line-height: 42px;
}
form input {
  width: 100%;
  min-width: 344px;
  color: #3F3F3F;
  background-color: #F8F8F8;
  padding: 12px;
  border: 1px solid #C4C4C4;
  border-radius: 6px;
  font-family: Nunito Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 21.82px;
  text-align: center;
}
form input:focus::placeholder {
  color: transparent;
}
form button {
  display: inline-flex;
  align-items: center;
  position: relative;
  text-align: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  transition: background-color 0.3s;
  padding: 16px 36px;
  border-radius: 6px;
  color: #fff;
  font-family: Nunito Sans;
  font-size: 14px;
  font-weight: 600;
  line-height: 19.1px;
  background-color: #34B7B9;
}
@media (hover: hover) {
  form button:hover {
    background-color: #2ea3a5;
  }
}
form a {
  font-family: Nunito Sans;
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
  text-align: center;
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #289A9C;
  transition: color 0.3s;
}
@media (hover: hover) {
  form a:hover {
    color: #157778;
  }
}
@media only screen and (max-width: 900px) {
  form {
    padding: 30px;
  }
  form h1 {
    font-size: 26px;
    line-height: 32px;
  }
  form input {
    min-width: 280px;
  }
}